"use scrict";
import MyForm from "@/components/MyForm/index.vue";
import MyFormItem from "@/components/MyFormItem/index.vue";
import MySelect from "@/components/MySelect/index.vue";
import { NOTICE_SELECT_RECEIVE } from "@/config/router.js";
import { ratingTypeOptions } from "@/config/filter.js";
import { COLOR_BACKGROUND_BOX } from "@/config/const.js";

export default {
	data() {
		return {
			detail: {
				name: "",
				receiverIds: "",
				subjectId: "",
				ratingType: "",
				deadline: ""
			},
			rules: {
				title: "作业标题不能为空",
				receiverIds: "接收对象不能为空",
				subjectId: "作业科目不能为空",
				content: "作业内容不能为空",
				retingType: "评分类型不能为空"
			},
			subjectOptions: [],
			ratingTypeOptions,
			receive: [],
			sendTime: {
				date: "",
				time: ""
			},
			COLOR_BACKGROUND_BOX,
			inTakePhoto: true
		}
	},
	computed: {
		receiveName: {
			get() {
				let res = [];
				this.receive.forEach(item => res.push(item.name));
				return res.join(',');
			},
			set(value) {
				this.receive = value;
				let res = [];
				value.forEach(item => res.push(item.id))
				this.detail.receiverIds = res;
			}
		},
		bindNoticeDate: {
			get() {
				return this.sendTime.date;
			},
			set(value) {
				this.sendTime.date = value;
				this.sendTime.time = this.sendTime.time || "00:00";
				this.$set(this.detail, "deadline", `${value} ${this.sendTime.time}:00`);
			}
		},
		bindNoticeTime: {
			get() {
				return this.sendTime.time;
			},
			set(value) {
				this.sendTime.time = value;
				this.sendTime.date = this.sendTime.date || time(Date.now());
				this.$set(this.detail, "deadline", `${this.sendTime.date} ${value}:00`);
			}
		}
	},
	onLoad(options) {
		this.$eventBus.$on("setWorkReceive", value => {
			if (!Array.isArray(value) || value.length === 0) return;
			this.receiveName = value;
		});
		this.$post(this.$api.GET_SUBJECT_LIST).then(res => {
			this.subjectOptions = res.data;
		});
	},
	methods: {
		linkToSelectReceive: function () {
			this.$toPage(NOTICE_SELECT_RECEIVE, {
				source: "work"
			});
		},
		takePhoto: function () {
			uni.chooseImage({
			  count: 1,
			  sizeType: ['original', 'compressed'],
			  sourceType: ['album', 'camera'],
			  success (res) {
			    // tempFilePath可以作为img标签的src属性显示图片
			    const tempFilePaths = res.tempFilePaths
			  }
			})
		},
		submit: function () {
			
		}
	},
	components: {
		MyForm,
		MyFormItem,
		MySelect
	}
}
